import React from "react"
import { Link } from "gatsby"
const ListLink = props => (
  <li style={{ display: `inline-block`, marginRight: `1rem` }}>
    <Link to={props.to}>{props.children}</Link>
  </li>
)

export default function Layout({ children }) {
  return (
    <div style={{ margin: `3rem auto`, maxWidth: 650, padding: `0 1rem` }}>
        <header style={{ marginBottom: `1.5rem` }}>
        <Link to="/" style={{ textShadow: `none`, backgroundImage: `none` }}>
          <h3 style={{ display: `inline` }}>Sahil Shembekar</h3>
        </Link>
        <ul style={{ listStyle: `none`, float: `right` }}>
          <ListLink to="/">Home</ListLink>
          <ListLink to="/work/">Work</ListLink>
          <ListLink to="/blog/">Blog</ListLink>
          {/* <ListLink to="/interests/">Interests</ListLink> */}
          {/* <ListLink to="/contact/">Contact</ListLink> */}
        </ul>
      </header>
      {/* <div style={{display: `block-inline`, marginRight: `1rem`, position: `absolute`, bottom: 0,left: 0, width: `100%`, height: `100px`, padding: `0 1rem`, margin: `3rem auto`, maxWidth: 650}}>
      <footer style={{display: `block-inline`, marginRight: `1rem`, position: `absolute`, bottom: 0,left: 0, width: `100%`, height: `100px`, padding: `0 1rem`, margin: `3rem auto`, maxWidth: 650}}>
          <a href="https://www.linkedin.com/in/smritirao03/">LinkedIn</a>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <a href="https://github.com/Smriti0311">GitHub</a>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <a href="https://smritirao.herokuapp.com/">Website</a>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <a href="https://twitter.com/SmritiRao9">Twitter</a>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <a href="mailto:smritigrao@gmail.com">Email</a>
      </footer>
      </div>  */}
      {children}
      
    </div>
    
    
    
  )
}